<template>
    <main>
        <form action="index.html" method="post">
            <table>
                <thead>
                    <caption><h1>Order</h1></caption>
                    <tr>
                        <td><strong>Item Name</strong></td>
                        <td><b>Quantity (in Carton)</b></td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in items" :key="index">
                        <td style="text-align:left;"> {{ item.name }} </td>
                        <td style="text-align:center;"> {{ item.quantity }} </td>
                        <td><button type="button" v-if="item.quantity<100" @click="item.quantity++"><strong>+</strong></button></td>
                        <td><button type="button" v-if="item.quantity>0" @click="item.quantity--"><strong>-</strong></button></td>
                    </tr>
                    <tr>
                        <td></td>
                        <td> Total :</td>
                        <td>{{ total }}</td>
                        <td></td>
                        <td> <input type="submit" class="button" value="Submit" v-if="total>0"> </td>
                    </tr>
                    <tr>
                        <input type="radio" name="orderType" v-model="orderType" value="State">State
                        <input type="radio" name="orderType" v-model="orderType" value="Inter-State">Inter-State
                    </tr>
                </tbody>
                <select name="orderOpt" v-model="orderOpt" placeholder="Select Any Option">
                    <option value="Deliver">Deliver It</option>
                    <option value="Pickup">Pickup By Myself</option>
                </select>
            </table>
        </form>
    </main>
</template>

<script>
    export default {
        name: 'order',
        data() {
          return{
            orderOpt:"Deliver",
            orderType:"State",
            items:[
            {
              name:"Doritos Nacho Cheese",
              quantity:0,
              },
            {
              name:"Kurkure Hyderbadi Hungama",
              quantity:0,
              },
            {
              name:"Kurkure Green Chutney",
              quantity:0,
              },
            {
              name:"Kurkure Masala Munch",
              quantity:0,
              },
            {
              name:"Kurkure Naughty Tomato",
              quantity:0,
              },
            {
              name:"Kurkure Solid Masti",
              quantity:0,
              },
            {
              name:"Lay's Chile LimÓn",
              quantity:0,
              },
            {
              name:"Lay's Classic Salted",
              quantity:0,
              },
            {
              name:"Lay's India's Magic Masala",
              quantity:0,
              },
            {
              name:"Lay's Spanish Tangy Tomato",
              quantity:0,
              },
            ]
          }
        },
        computed: {
            total(){
                let sum = 0;
                for (let i = 0; i < 10; i++) {
                    sum+=parseInt(this.items[i].quantity);
                }
                return sum;
            },
        },
        methods: {
            submit() {
                return this.data()
            }
        }
    };
</script>
<style scoped>
    table {
        position: relative;
        left: 40%;
        top: 30%;
    }
    button,.button {
        background-color: Tomato;
        border: 2px solid black;
    }
    table {
        text-align: center;
    }
    @media screen and (min-width: 720px) {
        button:active {
        background-color: darkblue;
        color: white;
        border: 1px solid black;
        }
    }
</style>
